import { FC, forwardRef } from 'react';
import { Box, BoxProps, MantineTheme } from '@mantine/core';
import { FormattedColor, TwBaseProps } from '../../types';
import ICONS from '../../constants/icons';
import { styled } from '../../utils';
import spriteFile from '../../icons/svg/sprite.symbol.svg';

export interface IconProps extends TwBaseProps {
  name: (typeof ICONS)[number];
  height?: number;
  width?: number;
  size?: number;
  color?: FormattedColor;
}

console.log('spriteFile:>>', spriteFile)

const Root = styled(Box)<BoxProps>(() => ({}));

export const Icon: FC<IconProps> = forwardRef(
  ({ name, width = 16, height = 16, size, color }, ref) => {
    const [clr, shade] = color ? color.split('.') : [];

    return (
      <Root
        ref={ref}
        component="svg"
        sx={(theme: MantineTheme) => {
          const colorStyle = clr && shade ? { fill: theme.colors[clr][+shade] } : {};

          return {
            ...colorStyle,
            width: size || width,
            height: size || height,
          };
        }}
      >
        <use xlinkHref={`${spriteFile}#${name}`} />
      </Root>
    );
  }
);
